<template>
  <div>
    <v-main>
      <v-layout style="background:#fff" class="py-5 px-3" align-center justify-center wrap column>
        <v-flex xs12 md12>
          <v-layout wrap>
            <v-flex xs12 md1></v-flex>
            <v-flex xs12 md7 pr-5>
              <p class="heading">Contact us</p>
              <p
                class="text"
              >Initiate a conversation with our team by contacting us through raccbemajestic29@gmail.com or +91-9442149340</p>
              <div class='onesignal-customlink-container'></div>
              <v-btn color="primary" @click="$router.push('/signup')">Join us</v-btn>
            </v-flex>
            <v-flex xs12 md4 hidden-sm-and-down>
              <v-img :src="contactuslogo" height="auto" width="50%"></v-img>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-main>
    <iframe name="iframe" style="display:none"></iframe>
    <v-layout row wrap align-center justify-center mt-3>
      <v-flex xs10 md7 text-center>
        <v-card class="mx-auto elevation-15" style="border-radius:8px" v-if="status">
          <v-card-text class="heading">
            <span style="color:#179E5B">Contact</span>
            <span style="color:#FBBD12">us</span>
          </v-card-text>
          <v-card-text>
            <v-form
              ref="form"
              v-model="valid"
              action="https://docs.google.com/forms/u/4/d/e/1FAIpQLSe1L9tbgBNcFPNzbYMhtTbb9_F3SAFZfjb0j1TD1s_M1-paWw/formResponse"
              method="POST"
              target="iframe"
            >
              <v-text-field
                :value="name"
                type="text"
                label="Name"
                name="entry.838116139"
                counter
                :rules="nameRules"
                color="blue"
              ></v-text-field>
              <v-text-field
                :value="value"
                label="Phone (prefer Whatsapp)"
                name="entry.246423705"
                color="green"
                counter
                :rules="phoneRules"
              ></v-text-field>
              <v-textarea
                :value="value"
                label="Quries"
                type="text"
                name="entry.2067760767"
                color="red"
                counter
                :rules="nameRules"
              ></v-textarea>
              <v-btn
                @click="callme"
                :disabled="!valid"
                type="submit"
                color="primary"
                v-show="show"
              >{{text}}</v-btn>

              <v-btn
                @click="callme2"
                color="red lighten-1"
                class="white--text"
                v-show="!show"
              >Clear All</v-btn>
            </v-form>
          </v-card-text>
        </v-card>

        <hr class="mt-3 mb-2" />
        <p>Get in Touch with us through Social Medias</p>
      </v-flex>
    </v-layout>

    <offline @detected-condition="handleConnectivityChange">
      <!-- Only renders when the device is online -->
      <div slot="online"></div>
      <!-- Only renders when the device is offline -->
      <div slot="offline"></div>
    </offline>

    <v-snackbar v-model="snackbar" color="green" :timeout="timeout">
      Thanks For your Response.
      We will respond you soon...!
      <v-btn color="white" @click="callme1" small class="black--text">Clear</v-btn>
    </v-snackbar>
    <v-snackbar
      v-if="!status"
      v-model="checknetwork"
      color="red"
      :timeout="timeoutstatus"
    >You are Offline, Check Your Network Connection.</v-snackbar>
  </div>
</template>

<script>
import contactus from "@/assets/logos/contact.svg";
import offline from "v-offline";

export default {
  components: {
    offline
  },
  data() {
    return {
      value1: "",
      value: "",
      snackbar: false,
      valid: true,
      text: "submit",
      show: true,
      timeout: 8000,
      timeoutstatus: 0,
      contactuslogo: contactus,
      checknetwork: true,
      name: "",
      nameRules: [v => !!v || "Required field"],
      status: true,
      phoneRules: [
          (v) => !!v || 'Name is required',
          (v) => v && v.length <= 10 || 'Number must be less than 10 characters'
        ],
    };
  },
  mounted() {
    document.title = "Rotract | contact";
  },
  methods: {
    handleConnectivityChange(status) {
      this.status = status;
    },
    callme() {
      this.text = "submit";
      this.snackbar = true;
      this.show = false;
    },
    callme1() {
      this.text = "submit";
      this.snackbar = false;
      this.$refs.form.reset();
      this.show = true;
    },
    callme2() {
      this.show = true;
      this.$refs.form.reset();
    }
  }
};
</script>

<style scoped>
.heading {
  font-size: 28px;
  color: #212529;
  font-family: "Josefin Sans", sans-serif;
}
.text {
  font-size: 16px;
  color: #212529;
  font-family: "Josefin Sans", sans-serif;
  text-align: justify;
  line-height: 1.8rem;
}
</style>